import axios from 'axios'

//  axios 
// axios  Promise
const fetch = axios.create({
    baseURL: '/api/',
    time: 1000,
    withCredentials: true // cookie
})

//  Koa  API 
const user = {
    getUser: 'user',
    signin: 'signin',
    signup: 'signup',
    logout: 'logout',
}

// category  API
const category = {
    findAll: 'cates',
    add: 'cate/add',
    edit: 'cate/edit',
    remove: 'cate/delete',
}

export {
    fetch,
    user,
    category, // API
}


